import React from 'react';
import PropTypes from 'prop-types';
import Button from '../Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import DctSvgIcon from '../Svg';
import Typography from '@material-ui/core/Typography';
import useStyles from './style';
import Container from '../../molecules/Container';

const Overlay = props => {
  const {
    children,
    title,
    primaryCTA,
    buttonProps,
    maxWidthOfDialog = '100%',
    className,
    isVisible,
    setIsVisible,
    addAnalyticsInfo,
    withButton = true,
    enableClickAway = false,
    ...other
  } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useStyles(props);

  const handleClickOpen = () => {
    addAnalyticsInfo && addAnalyticsInfo();
    setOpen(true);
    setIsVisible(true);
  };
  const handleClose = () => {
    setOpen(false);
    setIsVisible(false);
  };

  return (
    <Container>
      {withButton && (
        <Button
          component="span"
          buttonType="primary"
          field={primaryCTA}
          onClick={handleClickOpen}
          {...buttonProps}
        />
      )}
      <Dialog
        onClose={handleClose}
        open={open || isVisible}
        maxWidth={maxWidthOfDialog}
        className={`${classes.root} ${className}`}
        {...other}
      >
        <Button
          iconOnly={true}
          className={`${classes.closeButton} overlayCloseBtn`}
          onClick={handleClose}
        >
          <DctSvgIcon name="CloseIcon" />
        </Button>

        <MuiDialogTitle disableTypography>
          <Typography
            variant="h4"
            component="h2"
            className={`${className && className.title}`}
          >
            {title}
          </Typography>
        </MuiDialogTitle>
        {enableClickAway ? (
          <ClickAwayListener onClickAway={handleClose}>
            <MuiDialogContent
              dividers={false}
              className={className && className.content}
            >
              {children}
            </MuiDialogContent>
          </ClickAwayListener>
        ) : (
          <MuiDialogContent
            dividers={false}
            className={className && className.content}
          >
            {children}
          </MuiDialogContent>
        )}
      </Dialog>
    </Container>
  );
};

Overlay.propTypes = {
  isVisible: PropTypes.bool,
  setIsVisible: PropTypes.func,
};

Overlay.defaultProps = {
  isVisible: false,
  setIsVisible: () => { },
};

export default Overlay;
